<template>
<div class="main-container">
    <div class="main-content-c">
      <div style="margin-left:15%;width:60%;">
        <el-steps :active="0" finish-status="success" align-center>
          <el-step title="1. 产品排重检查进行中"></el-step>
          <el-step title="2. 产品信息录入未开始"></el-step>
          <el-step title="3. 发布产品未开始"></el-step>
        </el-steps>
      </div>
      <p class="p-title">基础信息</p>
      <p class="p-content-main">
        <el-row :gutter="20">
          <el-col :span="2" class="name-right">产品名称</el-col>
          <el-col :span="9">
          <el-form :model="check_data" :rules="aRules" ref="check_data">
          <el-form-item prop="name">
            <el-input v-model="check_data.name" placeholder="请输入内容" size="medium"></el-input>
          </el-form-item>
          </el-form>
          </el-col>
        </el-row>
      </p>
      <br>
      <br>
      <p class="p-title">产品图片</p>
      <p class="p-content-main">
        <el-row :gutter="20">
          <el-col :span="24">
            <el-upload
                action="http://img.orkotech.com/fdfs_upload.php"
                :limit=1
                :data="uploadParam"
                :before-upload="beforeUpload"
                :on-success="uploadSuccess"
                :on-error="uploadFail"
                list-type="picture-card"
                :on-exceed="overLimit"
                :on-preview="handlePictureCardPreview"
                :on-remove="handleRemove">
                <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
              <img width="100%" :src="dialogImageUrl" alt="">
            </el-dialog>
          </el-col>
        </el-row>
      </p>
    </div>

    <div style="border-top:1px solid #ccc;">
        <el-button @click="submit('check_data')" class="btn-save" plain>&nbsp;&nbsp;提交&nbsp;&nbsp;</el-button>
        <el-button class="btn-cancel" plain>&nbsp;&nbsp;取消&nbsp;&nbsp;</el-button>
    </div>
</div>
</template>

<script>
import axios from 'axios'
import Loading from 'element-ui';

export default {
    mounted() {// 载入时触发

    },
    data() {
        return {
            dialogImageUrl: '',
            dialogVisible: false,
            multiple_flag: false,
            up_flag: true,
            loading: '',
            formLabelWidth: '120px',
            dynamicTags: [],
            check_data: {
                name: '',
                img: '',
            },
            aRules: {
                name: [
                    {required: true, message:'请输入名称', trigger:'blur'},
                    {max:60, message: '超过名称最长字数限制60', trigger:'blur'},
                ],
            },
            uploadParam: {
                file_field: "file",//文件上传字段 将文件流保存到该值（eg:fdfs_file）
                token: "",//加密token md5( 加密salt值 + 13位时间戳 )
                time: +new Date(),//13位时间戳
                format: "",//上传的文件格式
                file: null//文件流
            },
        };
    },
    methods:{
        submit(formName) {
            let that = this;
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    const loading = this.$loading({
                        lock: true,
                        text: '产品图片正在排重...',
                        spinner: 'el-icon-loading',
                        background: 'rgba(0, 0, 0, 0.5)'
                    });
                    let url = "/v1/product/checks";
                    let params = {};
                    params.name = this.check_data.name;
                    params.img = this.check_data.img;
                    this.$axios.post(
                        url,
                        params,
                        {}
                    ).then((res) => {
                        loading.close();
                        this.$message({
                            type: 'success',
                            message: res.data.msg,
                            onClose: function(){
                                that.$router.push('/product/check/index');
                            },
                        });
                    }).catch((err) => {
                        loading.close();
                        this.$message.error(err.response.data.error);
                    });
                } else {
                    return false;
                }
            });
            
        },
        beforeUpload(file) {
            this.uploadParam.token = this.$md5('salt001'+this.uploadParam.time.toString());
            let fileType = file.type;
            this.uploadParam.format = fileType.slice(fileType.indexOf('/')+1);
            this.uploadParam.file = file;
        },
        uploadSuccess(response, file, fileList) {
            if (response.status == 0) {
                this.check_data.img = "http://img.orkotech.com/" + response.path;
            } else {
                this.$message.error(response.msg);
            }
        },
        uploadFail(err, file, fileList) {
            this.$message.error(err);
        },
        overLimit(file, fileList) {// 超出图片限制提示
            this.$message.error('最多上传一张图片');
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
        created() {
            let row = this.$route.query;
            this.check_data.name = row.name;
            this.check_data.img = '';
        },
    }
}
</script>

<style scoped>
/* 设置table trhover 字体 */
/* 字体hover主色 13c19f */
/* hover背景色 eef1f9 */
.name-right{
    font-size:14px;
    line-height:34px;
    text-align:right;
}
.p-title{
    margin:0 auto;color:#03b8cc;
}
.p-content-main{
    margin-top:20px;
}
.m_t_5px{
    margin-top:5px;
}
.btn-save{
    float:right;margin:8px;background-color:#13c19f;color:white;border-color:#13c19f;
    margin-right:11%;
}
.btn-cancel{
    float:right;margin:8px;margin-right:20px;
}
.p-span{
    text-align:left;float:left;font-size:14px;line-height:20px;color:#96a6b6;
}
.p-span-active{
    color:black;
}
.el-icon-plus{
    cursor:pointer;
}
.bottomBox {
  position: absolute;
  bottom: 0;
  width: 100%;
}
.el-loading-spinner .el-loading-text{
    font-size:20px;
}
</style>



